@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@400;600&display=swap);

.attribution {
    position: fixed;
    bottom: 0;
     font-size: 11px; 
     text-align: center; }
.attribution a { color: hsl(228, 45%, 44%); }
.mockups {
    width: 100%;
    height: auto;
  
}
.mockups-container{
    margin-bottom: 1.5rem;
}
.logo {
    width: 50%;
    max-width: 250px;
    height: auto;
    padding: 0.2rem 0.5rem;
    margin: 2rem 0;
}
.register-section{
    display: grid;
    place-items: center;
    text-align: center;
    color: white;
}
.register-section h4 {
    font-size: 1.3rem;
    max-width: 20ch;
    margin-top: 2rem;
    margin-bottom: 1rem;
}
.register-section p {
    font-size: 0.85rem;
    font-weight: 200;
    max-width: 30ch;
    margin-bottom: 1.4rem;
}
.register-btn{
    width: 70%;
    padding: 0.6rem 4rem;
    background-color: white;
    color: var(--PrimaryViolet);
    border-radius: 40px;
    margin-bottom: 2rem ;
    max-width: 300px;
}
:hover.register-btn{
    background-color: var(--PrimarySoftMagenta);
    color: white;
}
@media screen and (min-width: 1200px) {
    .register-section h4 {
        font-size: 1.6rem;
        max-width: 24ch;
        text-align: left;
    }
    .register-section p {
        font-size: 1.1rem;
        max-width: 40ch; 
        text-align: left;
        margin-left: 3rem;
    }
    .register-btn{
        text-align: center;
        max-width: 30ch;
        position: relative;
        left: -15%;
    }
    .register-section{
        place-items: left;
        grid-template-rows: 1fr 1fr 1fr;
    }
}
.fab{
    color: white;
    justify-self: center;
}
:hover.fab{
  color: var(--PrimarySoftMagenta);
}
#icons{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
    width: 40%;
    margin: 1rem auto;
    padding-bottom: 1rem;
}
:root{
    --PrimaryViolet:  hsl(257, 40%, 49%);
    --PrimarySoftMagenta: hsl(300, 69%, 71%);
    --fontPoppins: Poppins, sans-serif;
    --fontOpenSans:'Open Sans', sans-serif;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    font-family: Poppins, sans-serif;
    font-family: var(--fontPoppins);
    background: url(/huddle-landing-page-with-single-indtroduction/static/media/bg-mobile.41ba0830.svg),hsl(257, 40%, 49%);
    background: url(/huddle-landing-page-with-single-indtroduction/static/media/bg-mobile.41ba0830.svg),var(--PrimaryViolet);
    background-repeat: no-repeat;
    background-size: cover;
}
#app{
    width: 80%;
    margin: 0.2rem auto;
}

@media screen and (min-width: 1200px) {
    #main-wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    #icons{
        position: absolute;
        width: 10%;
        bottom: 1rem;
        right: 2rem;
    }
    #main-content, .intro-wrapper{
        width: 100%;
    }
    #main-content{
        padding-top: 25%;
        padding-bottom: 25%;
    }
    

    
}


